/* BASE */
html, body, #root, .Home {
    height: 100%;
}

body {
    background-color: #d4423b;
    color: #333;
    font-family: 'Barlow Semi Condensed', sans-serif;
    font-weight: 300;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

strong {
    font-weight: 500;
}

/* COMMON */
.full-section {
    position: relative;
    overflow: hidden;
    min-height: 80vh;
    height: 1px;
}

@media (min-width: 768px) {
    .full-section {
        height: 100%;
    }
}

.center-out {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.section-header {
    display: flex;
    flex-direction: column;
    margin-left: 5rem;
    margin-top: 2rem;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
}

.section-header .title {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin-bottom: 0.5rem;
    font-size: 2.25rem;
    --bg-opacity: 1;
    background-color: #000;
    background-color: rgba(0, 0, 0, var(--bg-opacity));
    --text-opacity: 1;
    color: #fff;
    color: rgba(255, 255, 255, var(--text-opacity));
    text-transform: uppercase;
}

.site-menu-1 {
    padding: 1.25rem;
}

.site-menu-1 li {
    list-style-type: none;
    margin: 0 0 0.25em;
    padding: 0;
    text-transform: uppercase;
}

.site-menu-1 li a:hover {
    color: #000;
}

.menu-dropdown .menu-toggle {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    background-color: #d4423b;
    display: inline-block;
    cursor: pointer;
}

.menu-dropdown .menu-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.25s;
}

.menu-dropdown .menu-content.is-open {
    max-height: 100vh;
}

