.home-landing .content {
    margin-left: 5rem;
    margin-top: 5rem;
    position: relative;
    z-index: 1;
    width: 240px;
}

.home-landing .content .logo {
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    --bg-opacity: 1;
    background-color: #fff;
    background-color: rgba(255, 255, 255, var(--bg-opacity));
    margin-bottom: 1rem;
}

/* HOME NEWS */
.home-news .cols {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-top: 4rem;
}

@media (min-width: 768px) {
    .home-news .cols {
        flex-direction: row;
    }
}

.home-news .cols .col {
    height: 100%;
}

@media (min-width: 768px) {
    .home-news .cols .col {
        width: 50%;
    }
}

.home-news .cols .col.agenda {
    --bg-opacity: 1;
    background-color: #fff;
    background-color: rgba(255, 255, 255, var(--bg-opacity));
}

.home-news .cols .col.noticias {
    padding: 2.5rem;
    background: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/img/temp-6.jpg) no-repeat 50% 50%;
    background-size: cover;
}

@media (min-width: 768px) {
    .home-news .cols .col.noticias {
        padding: 0;
        padding-left: 10rem;
    }
}

.home-news .cols .col.noticias .noticia {
    color: white;
}

.home-news .cols .col.noticias .noticia .content {
    padding: 1rem;
    margin-bottom: 0.5rem;
    background-color: rgba(0, 0, 0, 0.5);
}

.home-news .cols .col.noticias .noticia .content h3 {
    font-size: 1.25rem;
    margin-bottom: 0.25rem;
    text-transform: uppercase;
}

.home-news .cols .col.noticias .noticia .content p {
    font-size: 0.875rem;
}

.home-news .cols .col.noticias .noticia .leiamais {
    padding: 0.5rem;
    background-color: #d4423b;
}

.home-news .cols .col.noticias .noticia .leiamais:hover {
    background-color: white;
    color: #d4423b;
}

/* HOME CURSOS */
.home-curso {
    position: relative;
}

.home-curso .curso-swiper {
    margin-top: 4rem;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.home-curso .curso-swiper .swiper-nav {
    --bg-opacity: 1;
    background-color: #000;
    background-color: rgba(0, 0, 0, var(--bg-opacity));
    padding: 0.5rem;
    color: #fff;
    position: absolute;
    top: 70%;
    width: 200px;
    z-index: 1;
    cursor: pointer;
}

.home-curso .curso-swiper .swiper-nav.prev {
    left: 0;
    text-align: right;
}

.home-curso .curso-swiper .swiper-nav.next {
    right: 0;
}

.home-curso .curso-swiper .swiper-nav:hover {
    background-color: white;
    color: black;
}

.home-curso .swiper-container, .home-curso .swiper-wrapper, .home-curso .swiper-slide {
    height: 100%;
}

.home-curso .curso-slide {
    width: 100%;
    height: 100%;
    background-size: cover;
}

.home-curso .curso-info {
    display: flex;
    margin-left: 5rem;
    margin-top: 2rem;
    position: relative;
    z-index: 1;
}

.home-curso .curso-info .col-1 {
    width: 360px;
}

.home-curso .curso-info .col-1 .title {
    --bg-opacity: 1;
    background-color: #000;
    background-color: rgba(0, 0, 0, var(--bg-opacity));
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin-bottom: 0.5rem;
    font-size: 2.25rem;
    color: #fff;
    text-transform: uppercase;
}

.home-curso .curso-info .col-1 .text {
    --bg-opacity: 1;
    background-color: #fff;
    background-color: rgba(255, 255, 255, var(--bg-opacity));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-bottom: 0.5rem;
    margin-right: 0.5rem;
}

.home-curso .curso-info .col-1 .acessar a {
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    margin-right: 0.5rem;
    display: block;
    text-align: right;
}

.home-curso .curso-info .col-1 .acessar a:hover {
    background-color: white;
    color: #d4423b;
}

.home-curso .curso-info .col-2 .desc {
    --bg-opacity: 1;
    background-color: #fff;
    background-color: rgba(255, 255, 255, var(--bg-opacity));
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    width: 240px;
}

.dropdown {
    text-transform: uppercase;
    position: relative;
    cursor: pointer;
}

.dropdown .selected {
    --bg-opacity: 1;
    background-color: #fff;
    background-color: rgba(255, 255, 255, var(--bg-opacity));
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    font-size: 2.25rem;
    display: flex;
}

.dropdown .selected .droptitle {
    flex-grow: 1;
}

.dropdown .selected .dropicon {
    margin-left: 0.75rem;
    margin-right: 0.5rem;
    align-self: center;
}

.dropdown .options-viewport {
    position: relative;
}

.dropdown .options {
    margin-bottom: 0.5rem;
    font-size: 2.25rem;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s;
}

.dropdown .options li {
    --bg-opacity: 1;
    background-color: #fff;
    background-color: rgba(255, 255, 255, var(--bg-opacity));
    margin: 0;
    padding-left: 0.5rem;
    padding-right: 1rem;
    list-style-type: none;
}

.dropdown .options li:hover {
    background-color: #eee;
}

.dropdown.isdown .selected {
    color: #999;
}

.dropdown.isdown .options {
    max-height: 100vh;
}

/* HOME ORQUESTRAS */
.home-orquestras {
    padding-top: 4rem;
}

.home-orquestras .center-out {
    background: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/img/temp-6.jpg) no-repeat 50% 50%;
    background-size: cover;
    color: #fff;
}

.home-orquestras .center-in {
    width: 50%;
}

/* SINGLE PAGE */
.single-page {
    display: flex;
    flex-direction: column;
    margin-top: 4rem;
    background: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/img/temp-6.jpg) no-repeat 50% 50% fixed;
    background-size: cover;
}

.single-page .mobile-menu {
    margin-top: 2rem;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
}

.single-page .desktop-menu {
    display: none;
}

@media (min-width: 768px) {
    .single-page {
        flex-direction: row;
    }
    .single-page .mobile-menu {
        display: none;
    }
    .single-page .desktop-menu {
        display: block;
    }
}

.single-page .header {
    display: flex;
    flex-direction: column;
    margin-left: 4rem;
    margin-top: -2rem;
    position: sticky;
    top: 2em;
    min-width: 240px;
}

@media (min-width: 768px) {
    .single-page .header {
        margin-left: 5rem;
        margin-left: 2rem;
        margin-right: 2rem;
        margin-top: -2rem;
        margin-bottom: 2rem;
        margin-right: 0;
    }
}

.single-page .header .title {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin-bottom: 0.5rem;
    font-size: 2.25rem;
    --bg-opacity: 1;
    background-color: #000;
    background-color: rgba(0, 0, 0, var(--bg-opacity));
    --text-opacity: 1;
    color: #fff;
    color: rgba(255, 255, 255, var(--text-opacity));
    text-transform: uppercase;
}

.single-page .content-viewport {
    display: flex;
    justify-content: center;
    width: 100%;
}

.single-page .content-viewport .content {
    padding: 1.25rem;
    margin: 2rem;
    --bg-opacity: 1;
    background-color: #fff;
    background-color: rgba(255, 255, 255, var(--bg-opacity));
    width: 100%;
    min-height: 80vh;
}

@media (min-width: 768px) {
    .single-page .content-viewport .content {
        padding: 4rem;
        margin: 2.5rem;
        margin-top: 2rem;
    }
}

/* FOOTER */
.footer .footer-1 {
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #cfcfcf;
}

@media (min-width: 768px) {
    .footer .footer-1 {
        flex-direction: row;
        text-align: left;
        align-items: flex-start;
        padding-right: 5rem;
    }
}

.footer .footer-1 .footer-col {
    font-size: 0.875rem;
    margin: 1rem;
    min-width: 100px;
}

@media (min-width: 768px) {
    .footer .footer-1 .footer-col {
        margin-left: 2.5rem;
    }
}

.footer .footer-1 .footer-col h3 {
    margin-bottom: 0.5rem;
    text-transform: uppercase;
    font-weight: 500;
}

.footer .footer-1 .footer-col.social a {
    margin-right: 0.25rem;
    display: inline-block;
}

.footer .footer-2 {
    display: flex;
    flex-direction: column-reverse;
    padding: 2rem;
    justify-content: center;
    align-items: center;
    font-size: 0.875rem;
    background-color: #000;
    color: #fff;
}

.footer .footer-2 .col {
