/*tailwind start components */

.container {
  width: 100%;
}

@media (min-width: 640px) {
  .container {
    max-width: 640px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (min-width: 1024px) {
  .container {
    max-width: 1024px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

/*tailwind end components */

/* VARS */

$red: #d4423b;

$green: #8bc73b;

$blue: #3568fb;

$yellow: #ffa92e;

/* UTIL */

.bg-r { color: #fff; background-color: $red;
}

.bg-g { color: #fff; background-color: $green;
}

.bg-b { color: #fff; background-color: $blue;
}

.bg-y { color: #fff; background-color: $yellow;
}

/* BASE */

html, body, #root, .Home {
  height: 100%;
}

body {
  background-color: $red;
  color: #333;
  font-family: 'Barlow Semi Condensed', sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

strong {
  font-weight: 500;
}

/* COMMON */

.full-section {
  position: relative;
  overflow: hidden;
  min-height: 80vh;
  height: 1px;
  @media (min-width: 768px) {
    height: 100%;
  }
}

.center-out {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  .center-in {
  }
}

.section-header {
  display: flex;
  flex-direction: column;
  margin-left: 5rem;
  margin-top: 2rem;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  .title {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    margin-bottom: 0.5rem;
    font-size: 2.25rem;
    --bg-opacity: 1;
    background-color: #000;
    background-color: rgba(0, 0, 0, var(--bg-opacity));
    --text-opacity: 1;
    color: #fff;
    color: rgba(255, 255, 255, var(--text-opacity));
    text-transform: uppercase;
  }
}

.site-menu-1 {
  padding: 1.25rem;
  li {
    list-style-type: none;
    margin: 0 0 0.25em;
    padding: 0;
    text-transform: uppercase;
    a {
      &:hover {
        color: #000;
      }
    }
  }
}

.menu-dropdown {
  .menu-toggle {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    background-color: $red;
    display: inline-block;
    cursor: pointer;
  }
  .menu-content {
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.25s;
    &.is-open {
      max-height: 100vh;
    }
  }
}

/* HOME LANDING */

@import "slideshow";

.home-landing {
  .content {
    margin-left: 5rem;
    margin-top: 5rem;
    position: relative;
    z-index: 1;
    width: 240px;
    .logo {
      padding-left: 1rem;
      padding-right: 1rem;
      padding-top: 0.75rem;
      padding-bottom: 0.75rem;
      --bg-opacity: 1;
      background-color: #fff;
      background-color: rgba(255, 255, 255, var(--bg-opacity));
      margin-bottom: 1rem;
    }
  }
}

/* HOME NEWS */

.home-news {
  .cols {
    display: flex;
    flex-direction: column;
    height: 100%;
    padding-top: 4rem;
    @media (min-width: 768px) {
      flex-direction: row;
    }
    .col {
      height: 100%;
      @media (min-width: 768px) {
        width: 50%;
      }
      &.agenda {
        --bg-opacity: 1;
        background-color: #fff;
        background-color: rgba(255, 255, 255, var(--bg-opacity));
      }
      &.noticias {
        padding: 2.5rem;
        background: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/img/temp-6.jpg) no-repeat 50% 50%;
        background-size: cover;
        @media (min-width: 768px){
          padding: 0;

          padding-left: 10rem;
        }
        .noticia {
          color: white;
          .content {
            padding: 1rem;
            margin-bottom: 0.5rem;
            background-color: rgba(0,0,0,.5);
            h3 {
              font-size: 1.25rem;
              margin-bottom: 0.25rem;
              text-transform: uppercase;
            }
            p {
              font-size: 0.875rem;
            }
          }
          .leiamais {
            padding: 0.5rem;
            background-color: $red;
            &:hover {
              background-color: white;
              color: $red;
            }
          }
        }
      }
    }
  }
}

/* HOME CURSOS */

.home-curso {
  position: relative;
  .curso-swiper {
    margin-top: 4rem;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
    .swiper-nav {
      --bg-opacity: 1;
      background-color: #000;
      background-color: rgba(0, 0, 0, var(--bg-opacity));
      padding: 0.5rem;
      color: #fff;
      position: absolute;
      top: 70%;
      width: 200px;
      z-index: 1;
      cursor: pointer;
      &.prev {
        left: 0;
        text-align: right;
      }
      &.next {
        right: 0;
      }
      &:hover {
        background-color: white;
        color: black;
      }
    }
  }
  .swiper-container, .swiper-wrapper, .swiper-slide {
    height: 100%;
  }
  .curso-slide {
    width: 100%;
    height: 100%;
    background-size: cover;
  }
  .curso-info {
    display: flex;
    margin-left: 5rem;
    margin-top: 2rem;
    position: relative;
    z-index: 1;
    .col-1 {
      width: 360px;
      .title {
        --bg-opacity: 1;
        background-color: #000;
        background-color: rgba(0, 0, 0, var(--bg-opacity));
        padding-left: 0.5rem;
        padding-right: 0.5rem;
        margin-bottom: 0.5rem;
        font-size: 2.25rem;
        color: #fff;
        text-transform: uppercase;
      }
      .text {
        --bg-opacity: 1;
        background-color: #fff;
        background-color: rgba(255, 255, 255, var(--bg-opacity));
        padding-left: 1rem;
        padding-right: 1rem;
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        margin-bottom: 0.5rem;
        margin-right: 0.5rem;
      }
      .acessar {
        a {
          padding-left: 1rem;
          padding-right: 1rem;
          padding-top: 0.5rem;
          padding-bottom: 0.5rem;
          margin-right: 0.5rem;
          display: block;
          @extend .bg-r;
          text-align: right;
          &:hover {
            background-color: white;
            color: $red;
          }
        }
      }
    }
    .col-2 {
      .desc {
        --bg-opacity: 1;
        background-color: #fff;
        background-color: rgba(255, 255, 255, var(--bg-opacity));
        padding-left: 1rem;
        padding-right: 1rem;
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        width: 240px;
      }
    }
  }
}

.dropdown {
  text-transform: uppercase;
  position: relative;
  cursor: pointer;
  .selected {
    --bg-opacity: 1;
    background-color: #fff;
    background-color: rgba(255, 255, 255, var(--bg-opacity));
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    font-size: 2.25rem;
    display: flex;
    .droptitle {
      flex-grow: 1;
    }
    .dropicon {
      margin-left: 0.75rem;
      margin-right: 0.5rem;
      align-self: center;
    }
  }
  .options-viewport {
    position: relative;
  }
  .options {
    margin-bottom: 0.5rem;
    font-size: 2.25rem;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s;
    li {
      --bg-opacity: 1;
      background-color: #fff;
      background-color: rgba(255, 255, 255, var(--bg-opacity));
      margin: 0;
      padding-left: 0.5rem;
      padding-right: 1rem;
      list-style-type: none;
      &:hover {
        background-color: #eee;
      }
    }
  }
  &.isdown {
    .selected {
      color: #999;
    }
    .options {
      max-height: 100vh;
    }
  }
}

/* HOME ORQUESTRAS */

.home-orquestras {
  padding-top: 4rem;
  .center-out {
    background: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/img/temp-6.jpg) no-repeat 50% 50%;
    background-size: cover;
    color: #fff;
  }
  .center-in {
    width: 50%;
  }
}

/* SINGLE PAGE */

.single-page {
  display: flex;
  flex-direction: column;
  margin-top: 4rem;
  background: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url(/img/temp-6.jpg) no-repeat 50% 50% fixed;
  background-size: cover;
  .mobile-menu {
    margin-top: 2rem;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
  }
  .desktop-menu {
    display: none;
  }
  @media (min-width: 768px) {
    flex-direction: row;

    .mobile-menu {
      display: none;
    }

    .desktop-menu {
      display: block;
    }
  }
  .header {
    display: flex;
    flex-direction: column;
    margin-left: 4rem;
    margin-top: -2rem;
    position: sticky;
    top: 2em;
    min-width: 240px;
    @media (min-width: 768px) {
      margin-left: 5rem;

      margin-left: 2rem;

      margin-right: 2rem;

      margin-top: -2rem;

      margin-bottom: 2rem;

      margin-right: 0;
    }
    .title {
      padding-left: 0.5rem;
      padding-right: 0.5rem;
      margin-bottom: 0.5rem;
      font-size: 2.25rem;
      --bg-opacity: 1;
      background-color: #000;
      background-color: rgba(0, 0, 0, var(--bg-opacity));
      --text-opacity: 1;
      color: #fff;
      color: rgba(255, 255, 255, var(--text-opacity));
      text-transform: uppercase;
    }
  }
  .content-viewport {
    display: flex;
    justify-content: center;
    width: 100%;
    .content {
      padding: 1.25rem;
      margin: 2rem;
      --bg-opacity: 1;
      background-color: #fff;
      background-color: rgba(255, 255, 255, var(--bg-opacity));
      width: 100%;
      min-height: 80vh;
      @media (min-width: 768px) {
        padding: 4rem;

        margin: 2.5rem;

        margin-top: 2rem;
      }
    }
  }
}

/* FOOTER */

.footer {
  .footer-1{
    padding: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    background-color: #cfcfcf;
    @media (min-width: 768px) {
      flex-direction: row;

      text-align: left;

      align-items: flex-start;

      padding-right: 5rem;
    }
    .footer-col {
      font-size: 0.875rem;
      margin: 1rem;
      min-width: 100px;
      @media (min-width: 768px) {
        margin-left: 2.5rem;
      }
      h3 {
        margin-bottom: 0.5rem;
        text-transform: uppercase;
        font-weight: 500;
      }
      &.social {
        a {
          margin-right: 0.25rem;
          display: inline-block;
        }
      }
    }
  }
  .footer-2{
    display: flex;
    flex-direction: column-reverse;
    padding: 2rem;
    justify-content: center;
    align-items: center;
    font-size: 0.875rem;
    background-color: #000;
    color: #fff;
    .col {
      margin-top: 1rem;
      margin-bottom: 1rem;
    }
    @media (min-width: 768px) {
      flex-direction: row;

      .col.copyright {
        width: 33.333333%;
      }

      .col.marcas {
        width: 66.666667%;
      }
    }
  }
}

@media (min-width: 768px) {
